<template>
  <section
    class="about"
    v-waypoint="{
      active: true,
      callback: onWaypoint,
      options: optionsWaypoint,
    }"
  >
    <div class="container">
      <div class="about__wrapper">
        <img
          src="../../../../assets/svg/about/crypto--module.svg"
          class="about__image"
          alt="crypto"
        />
        <p class="about__description">
          Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do
          eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad
          minim veniam, quis nostrud exercitation ullamco laboris nisi ut
          aliquip ex ea commodo consequat.
        </p>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'About',
  data() {
    return {
      optionsWaypoint: {
        root: null,
        rootMargin: '0px 0px 0px 0px',
        threshold: [0, 1],
      },
      isShow: false,
    };
  },
  methods: {
    onWaypoint({ going }) {
      if (going === this.$waypointMap.GOING_IN) {
        const description = document.querySelector('.about__description');
        const image = document.querySelector('.about__image');

        description.classList.add('animated', 'fadeInRight');
        image.classList.add('animated', 'fadeInLeft');
      }
    },
  },
};
</script>

