<template>
  <li class="currency">
    <Icon :icon="['currency__icon', icon]" />
    <h2 class="currency__title">{{ title }}</h2>
    <h3 class="currency__subtitle">{{ shortTitle }}</h3>
    <span class="currency__rate">USD {{ rate }}</span>
    <span class="currency__rate-percent">{{ ratePercent }}%</span>
    <a :href="link" class="currency__link">
      <Icon icon="charts" />
    </a>
  </li>
</template>

<script>
import Icon from '@/components/partial/Icon.vue'

import axios from 'axios'

export default {
  name: 'Currency',
  components: { Icon },
  props: {
    title: {
      type: String,
      default: 'Bitcoin',
    },
    shortTitle: {
      type: String,
      default: 'BTH',
    },
    link: {
      type: String,
      default: '#',
    },
    icon: {
      type: String,
      default: 'bitcoin',
    },
  },
  data: () => ({
    rate: 0,
    ratePercent: 0
  }),
  mounted() {
    const pending = []

    pending.push(
      axios.get(
        `https://min-api.cryptocompare.com/data/generateAvg?fsym=${this.shortTitle}&tsym=USD&e=Kraken`,
      ),
    )
    pending.push(
      axios.get(
        `https://min-api.cryptocompare.com/data/price?fsym=${this.shortTitle}&tsyms=USD`,
      ),
    )

    Promise.all(pending).then(([{ data: history }, { data: current }]) => {
      this.rate = current.USD
      this.ratePercent = history.DISPLAY.CHANGEPCT24HOUR
    })
  },
}
</script>

