<template>
  <div class="scroll" v-if="computedOn && visible">
    <div
      class="arrow"
      :style="{
        transform: arrowRotation,
      }"
      @click="arrowGo"
    ></div>
    <div v-if="isSliderScroll" class="slider">
      <span
        v-for="(s, i) in slides"
        :key="i"
        :class="[slide === i ? 'active' : '', computedTheme]"
        @click="goto(i)"
      ></span>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'slider-points',
  props: {
    on: {
      type: Boolean,
      default: true,
    },
  },
  data: () => ({
    slide: 0,
    permit: true,
    slides: [
      '.welcome',
      '.currencies',
      '.advantages-group',
      '.rates',
      '.owl-dot',
      '.about',
    ],
    scrollData: {
      y: 0,
    },
  }),
  computed: {
    ...mapGetters({
      signInVisibility: 'popups/signInVisibility',
      signUpVisibility: 'popups/signUpVisibility',
    }),
    visible() {
      return !this.signUpVisibility && !this.signInVisibility
    },
    computedOn() {
      return document.body.clientWidth > 994
    },
    computedTheme() {
      return this.slide === 2 ? 'light' : 'primary'
    },
    isSliderScroll() {
      return true
    },
    arrowRotation() {
      return this.slide === this.slides.length - 1
        ? 'rotate(225deg)'
        : 'rotate(45deg)'
    },
  },
  methods: {
    arrowGo() {
      if (this.slide == this.slides.length - 1) {
        this.slide = -1
        this.next()
      } else {
        this.next()
      }
    },
    prev() {
      var self = this,
        slideElement = null

      if (self.permit) {
        this.slide -= this.slide > 0 ? 1 : 0
        slideElement = document.querySelector(this.slides[this.slide])
        slideElement?.scrollIntoView({ block: 'center', behavior: 'smooth' })
        this.permit = false
        setTimeout(() => {
          slideElement?.scrollIntoView({ block: 'center', behavior: 'smooth' })
          self.permit = true
        }, 800)
      }
    },
    next() {
      var self = this,
        slideElement = null

      if (self.permit) {
        this.slide += this.slide < this.slides.length - 1 ? 1 : 0
        slideElement = document.querySelector(this.slides[this.slide])
        slideElement?.scrollIntoView({ block: 'center', behavior: 'smooth' })
        this.permit = false
        setTimeout(() => {
          slideElement?.scrollIntoView({ block: 'center', behavior: 'smooth' })
          self.permit = true
        }, 800)
      }
    },
    goto(slide) {
      this.slide = slide
      var slideElement = document.querySelector(this.slides[this.slide])
      slideElement?.scrollIntoView({ block: 'center', behavior: 'smooth' })
    },
  },
  mounted() {
    this.$anime({
      targets: this.$el.querySelector('.arrow'),
      bottom: '30px',
      loop: true,
      direction: 'alternate',
      duration: 1000,
      easing: 'easeInOutCubic',
    })

    const idlePeriod = 50
    const self = this
    const animationDuration = 1000

    let lastAnimation = 0

    document.body.addEventListener('wheel', event => {
      var delta = event.wheelDelta
      var timeNow = new Date().getTime()
      if (timeNow - lastAnimation < idlePeriod + animationDuration) {
        return
      }

      if (self.permit) {
        if (delta < 0) {
          this.next()
        } else {
          this.prev()
        }
      }

      lastAnimation = timeNow
    })
  },
  destroyed() {
    document.body.removeEventListener('wheel', {})
  },
}
</script>

<style lang="sass">
@import '../../scss/variables'

.arrow
  width: 50px
  height: 50px
  transform: rotate(45deg)
  border-right: white 2px solid
  border-bottom: white 2px solid
  position: fixed
  z-index: 999
  bottom: 50px
  left: calc(50vw - 25px)
  cursor: pointer

.slider
    position: fixed
    z-index: 999
    display: flex
    top: 44vh
    right: 50px
    flex-direction: column
    height: 200px

    span.active
        opacity: 1

    span
        background: linear-gradient(45deg, #23c3fa, #1477c9)
        margin-top: 15px
        width: 16px
        height: 16px
        border-radius: 100%
        opacity: .4
        transition: all .6s

        &:hover
            cursor: pointer

    span.light
      background: white
</style>
